import { Component, Prop, Watch, h, Element } from '@stencil/core';

@Component({
	tag: 'o-radio',
	styleUrl: 'o-radio.scss',
	shadow: true
})
export class ORadio {
	@Element() el: HTMLORadioElement;

	/** @prop {string} nameValue - The id attribute for the radio input. */
	@Prop() nameValue: string = 'default';

	/** @prop {string} value - The value attribute for the radio input. */
	@Prop({ reflect: true }) value: string;

	/** @prop {boolean} checked - Determines the initial checked state of the radio. Default is false. */
	@Prop({ mutable: true }) checked: boolean = false;

	/** @prop {boolean} disabled - Whether the input is disabled. Default is false. */
	@Prop({ reflect: true }) disabled: boolean = false;

	/** @prop {string} name - The name attribute for the radio input. Used to bind radios together in a radio group. */
	@Prop({ reflect: true }) name: string = 'default';

	/** @prop {string} label - The label text for the radio input. */
	@Prop({ reflect: true }) label: string;

	/** Returns the HTMLInputElement if found. */
	private getRadioElement(): HTMLInputElement | null {
		return this.el.shadowRoot?.querySelector('input[type="radio"]');
	}

	/** Watches for changes to the 'checked' property. */
	@Watch('checked')
	watchChecked(newValue: boolean) {
		const radio = this.getRadioElement();
		if (radio) {
			radio.checked = newValue;
		}
	}

	render() {
		const disabledClasses = this.disabled
			? 'disabled:border-form-color-border-disabled disabled:bg-form-color-background-disabled disabled:checked:bg-background-brand-knockout-disabled disabled:cursor-not-allowed'
			: '';

		return (
			<div class="mb-3 flex items-center antialiased">
				<input
					id={this.nameValue}
					type="radio"
					class={`appearance-none bg-form-color-background-default border-width-sm checked:bg-background-brand-knockout checked:hover:bg-background-brand-knockout checked:focus:bg-background-brand-knockout focus:outline-width-lg focus:outline-focus-ring-color-border-default focus:outline-offset-0 ${disabledClasses}`}
					value={this.value}
					checked={this.checked}
					disabled={this.disabled}
					name={this.name}
				/>
				<slot></slot>
				<span class="ml-2 flex items-center">
					<o-label
						htmlFor={this.nameValue}
						label={this.label}
						variant={this.disabled ? 'disabled' : undefined}
					></o-label>
				</span>
			</div>
		);
	}
}
